<template>
    <div class="home">
        <div class="mainCanvas" :style="{width:1000/scale+ 'px',height:1000/scale+ 'px'}">
            <template v-for="rights,index in rightsArray">
                <span v-for="right in rights"
                      :style="{left:right[0]/scale +'px',top:right[1]/scale +'px',width:round+'px',height:round+'px',borderRadius:round+'px',backgroundColor: colors[index]}"
                      @click="showInfo(right)"></span>
            </template>
        </div>
        <div class="mainColors">
            <p v-for="value,index in colors" :style="{backgroundColor:value}"></p>
        </div>
        <div class="mainInput">
            <p>
                <label>第一段颜色值</label>
                <button @click="changeFirstColor=!changeFirstColor">Edit</button>
                <photoshop-picker v-if="changeFirstColor" @ok="changeFirstColor=false" @cancel="changeFirstColor=false"
                                  v-model="firstColor"/>
                <!--                <input v-model="startColor">-->
            </p>
            <p>
                <label>第二段颜色值</label>
                <button @click="changeSecondColor=!changeSecondColor">Edit</button>
                <photoshop-picker v-if="changeSecondColor" @ok="changeSecondColor=false" @cancel="changeSecondColor=false"
                                  v-model="secondColor"/>
                <!--                <input v-model="midColor">-->
            </p>
            <p>
                <label>第三段颜色值</label>
                <button @click="changeThirdColor=!changeThirdColor">Edit</button>
                <photoshop-picker v-if="changeThirdColor" @ok="changeThirdColor=false" @cancel="changeThirdColor=false"
                                  v-model="endColor"/>
                <!--                <input v-model="endColor">-->
            </p>
            <p>
                <label>第四段颜色值</label>
                <button @click="changeForthColor=!changeForthColor">Edit</button>
                <photoshop-picker v-if="changeForthColor" @ok="changeForthColor=false" @cancel="changeForthColor=false"
                                  v-model="forthColor"/>
                <!--                <input v-model="endColor">-->
            </p>
            <p>
                <label>第五段颜色值</label>
                <button @click="changeFifthColor=!changeFifthColor">Edit</button>
                <photoshop-picker v-if="changeFifthColor" @ok="changeFifthColor=false" @cancel="changeFifthColor=false"
                                  v-model="fifthColor"/>
                <!--                <input v-model="endColor">-->
            </p>
            <p>
                <label>第六段颜色值</label>
                <button @click="changeSixthColor=!changeSixthColor">Edit</button>
                <photoshop-picker v-if="changeSixthColor" @ok="changeSixthColor=false" @cancel="changeSixthColor=false"
                                  v-model="sixthColor"/>
                <!--                <input v-model="endColor">-->
            </p>
            <p>
                <label>分块数目</label>
                <input type="number" v-model="colorSteps">
            </p>
            <p>
                <label>点半径</label>
                <input type="number" v-model="round">
            </p>
            <p>
                <button @click="init">重新初始化</button>
            </p>
        </div>
        <div class="mainButton">
            <button @click="zoom('+')">ZoomIn(+)</button>
            <button @click="zoom('reset')">Reset</button>
            <button @click="zoom('-')"> ZoomOut(-)</button>
        </div>
        <div class="mainInfo" v-if="selected">
            <button @click="selected=false">close</button>
            <p><span>X:</span>{{selectInfo[0]}}</p>
            <p><span>Y:</span>{{selectInfo[1]}}</p>
            <p><span>Weight:</span>{{selectInfo[2]}}</p>

            <p><img :src="'./stateImg/state-'+(selectInfo[3]-1)+'.png'"></p>
            <p>State</p>
            <p><img :src="'./nextStateImg/nextState-'+(selectInfo[3]-1)+'.png'"></p>
            <p>nextStateImg</p>
        </div>
    </div>
</template>

<script>
    import {Swatches, Material, Compact, Slider, Sketch, Chrome, Photoshop} from 'vue-color'

    export default {
        name: 'home',
        components: {
            'material-picker': Material,
            'compact-picker': Compact,
            'swatches-picker': Swatches,
            'slider-picker': Slider,
            'sketch-picker': Sketch,
            'chrome-picker': Chrome,
            'photoshop-picker': Photoshop
        },
        computed: {
            firstColorC() {
                return this.firstColor.hex
            }, secondColorC() {
                return this.secondColor.hex
            }, thirdColorC() {
                return this.thirdColor.hex
            }, forthColorC() {
                return this.forthColor.hex
            }, fifthColorC() {
                return this.fifthColor.hex
            }, sixthColorC() {
                return this.sixthColor.hex
            },

        },
        data() {
            return {
                scale: 1.0,
                res: [],
                firstColor: {hex: "#6ab92c"},
                changeFirstColor: false,
                secondColor: {hex: "#aed606"},
                changeSecondColor: false,
                thirdColor: {hex: "#fef804"},
                changeThirdColor: false,
                forthColor: {hex: "#ff7b00"},
                changeForthColor: false,
                fifthColor: {hex: "#db253e"},
                changeFifthColor: false,
                sixthColor: {hex: "#5b1875"},
                changeSixthColor: false,
                colorSteps: 30,
                colors: [],
                rightsArray: [],
                round: 3,
                selected: false,
                selectInfo: [0, 0, 0]
            }
        },
        methods: {
            zoom(param) {
                switch (param) {
                    case "reset":
                        this.scale = 1.0;
                        break;
                    case "+":
                        this.scale -= 0.1;
                        break;
                    case "-":
                        this.scale += 0.1;
                        break;
                }
            },
            init() {
                let minRight = 0, maxRight = 0;
                let data = this.res.data
                if (data.length > 0) {
                    minRight = data[0][2];
                    maxRight = data[data.length - 1][2]
                }
                console.log(minRight, maxRight);
                let colors1 = this.gradientColor(this.firstColorC, this.secondColorC, Math.ceil(this.colorSteps / 2));
                let colors2 = this.gradientColor(this.secondColorC, this.thirdColorC, Math.floor(this.colorSteps / 2));
                let colors3 = this.gradientColor(this.thirdColorC, this.forthColorC, Math.floor(this.colorSteps / 2));
                let colors4 = this.gradientColor(this.forthColorC, this.fifthColorC, Math.floor(this.colorSteps / 2));
                let colors5 = this.gradientColor(this.fifthColorC, this.sixthColorC, Math.floor(this.colorSteps / 2));
                this.colors = colors1.concat(colors2, colors3, colors4, colors5);
                console.log(this.colors)
                let rightsArrayLength = Math.ceil(data.length / this.colorSteps);//权重分组长度
                this.rightsArray = [];
                for (let i = 0; i < this.colorSteps - 1; i++) {
                    this.rightsArray.push(data.slice(i * rightsArrayLength, (i + 1) * rightsArrayLength))
                }
                this.rightsArray.push(data.slice((this.colorSteps - 1) * rightsArrayLength, data.length));
                console.log(this.rightsArray);


            },
            showInfo(right) {
                this.selected = true;
                this.selectInfo = right;
                console.log(right);
            },
            gradientColor(startColor, endColor, step) {
                let startRGB = this.colorRgb(startColor);//转换为rgb数组模式
                let startR = startRGB[0];
                let startG = startRGB[1];
                let startB = startRGB[2];

                let endRGB = this.colorRgb(endColor);
                let endR = endRGB[0];
                let endG = endRGB[1];
                let endB = endRGB[2];

                let sR = (endR - startR) / step;//总差值
                let sG = (endG - startG) / step;
                let sB = (endB - startB) / step;

                var colorArr = [];
                for (var i = 0; i < step; i++) {
                    //计算每一步的hex值
                    var hex = this.colorHex('rgb(' + parseInt((sR * i + startR)) + ',' + parseInt((sG * i + startG)) + ',' + parseInt((sB * i + startB)) + ')');
                    colorArr.push(hex);
                }
                return colorArr;
            },
            // 将hex表示方式转换为rgb表示方式(这里返回rgb数组模式)
            colorRgb(sColor) {
                var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
                var sColor = sColor.toLowerCase();
                if (sColor && reg.test(sColor)) {
                    if (sColor.length === 4) {
                        var sColorNew = "#";
                        for (var i = 1; i < 4; i += 1) {
                            sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
                        }
                        sColor = sColorNew;
                    }
                    //处理六位的颜色值
                    var sColorChange = [];
                    for (var i = 1; i < 7; i += 2) {
                        sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
                    }
                    return sColorChange;
                } else {
                    return sColor;
                }
            },
            // 将rgb表示方式转换为hex表示方式
            colorHex(rgb) {
                var _this = rgb;
                var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
                if (/^(rgb|RGB)/.test(_this)) {
                    var aColor = _this.replace(/(?:(|)|rgb|RGB)*/g, "").split(",");
                    var strHex = "#";
                    for (var i = 0; i < aColor.length; i++) {
                        var hex = Number(aColor[i]).toString(16);
                        hex = hex < 10 ? 0 + '' + hex : hex;// 保证每个rgb的值为2位
                        if (hex === "0") {
                            hex += hex;
                        }
                        strHex += hex;
                    }
                    if (strHex.length !== 7) {
                        strHex = _this;
                    }
                    return strHex;
                } else if (reg.test(_this)) {
                    var aNum = _this.replace(/#/, "").split("");
                    if (aNum.length === 6) {
                        return _this;
                    } else if (aNum.length === 3) {
                        var numHex = "#";
                        for (var i = 0; i < aNum.length; i += 1) {
                            numHex += (aNum[i] + aNum[i]);
                        }
                        return numHex;
                    }
                } else {
                    return _this;
                }
            }
        },
        mounted() {
            this.$axios.get("./data_json.json").then((res) => {
                this.res = res.data;
                this.init();
            })
        }
    }
</script>
